@import url('https://fonts.googleapis.com/css?family=Pirata+One|Rubik:900');


.home-main-model{
    height: 100%;
    width: 100%;
   background-color: rgba(0, 0, 0, 0.945);
    overflow-x: none;
}



.home-model-forntpage{
    height: 100vh;
    width: 100%;
