}


/* ----------------------------Background------------------------------------------------------------------------------ */
.background-model{
    min-height: 100vh;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.945);
}

.video-wrap {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
    
    pointer-events: none;
  }
  
  #video-bg {
    position: absolute;
    width: 100%;
    min-height: 100vh;
    height: 100%;
    min-width: 100%;
    background-position: center center;
    background-size: cover;
    object-fit: cover;
    transform: rotate(180deg);
  }

  .content{
    position: relative;
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    gap: 3vh;
    justify-content: center;
    align-items: center;
    text-align: center;
  }

  .hero-title {
    font-size: 8vw;
    line-height: 1em;
    font-weight: 900;
    font-family: 'Rubik', sans-serif;
  
  }

