  }
  
/* ----------------------------Background------------------------------------------------------------------------------ */

.front-model-left {
    width: 60%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
    padding: 0rem 4.1vw;
}

.front-left-inner-div{
    display: flex;
    text-align: center;
    flex-direction: column;
    gap: 2vh;
}



    


.interior-text{
    margin-top: -2vh;
    font-family: 'Pirata One', cursive ;
