}

.contact-studio-model{
    margin-top: 7%;
}

.contact-blocks p{
	color: rgb(153, 153, 153);
	font-size: small;
	line-height: 17px;
}

.contact-connect-icons{
	display: flex;
	gap: 1rem;
}

.GIT-icons{
	width: 3vw !important;
	height: 6vh !important;
	
    transition: .3s all ease;
}

.GIT-icons:hover{
	
	filter: grayscale(0%);
}
/*---------------------Button------------------------------ Button */

.cta {
    border: none;
    background: none;
    display: flex;

    cursor: pointer;
    color: white;
  }

  .service-arrow-added{
    height: 4vh;
    width: 2vw;
  }
  
  .cta span {
    padding-bottom: 7px;
    letter-spacing: 4px;
    font-size: 3vh;
    color: white;
    padding-right: 15px;
    text-transform: capitalize;
  }
  

  

  
  .hover-underline-animation {
    position: relative;
    color: white;
  
    padding-bottom: 20px;
  }
  
  .hover-underline-animation:after {
    content: "";
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 2px;
    bottom: 0;
    left: 0;
    background-color:  rgba(253, 0, 0, 0.637);
 
    transform-origin: bottom right;
    transition: transform 0.25s ease-out;
  }
  
  .cta:hover .hover-underline-animation:after {
    transform: scaleX(1);
    transform-origin: bottom left;
  }
  

