.service-page-model{
    min-height: 100vh;
    max-height: 100%;
    overflow-x: none;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.945);
}

.service-model-top{
    height: 70vh;
    position: relative;
    width: 100%;
    padding: 1vh 4.1vw;
    
    display: flex;
    color: white;
    flex-direction: column;
    align-items: center;
    
    justify-content: center;
}

.service-model-top h3{
    margin-bottom: 2vh;
    letter-spacing: 2px;
    z-index: 100;
    font-size: 2.5vh;
    color: rgba(231, 4, 4, 0.781);
}

.service-model-top h2{
    font-family: var(--font-monExtra);
    font-size: 7vh;
    z-index: 100;
    width: 45%;
    text-align: center;
    letter-spacing: 2px;
    text-transform: uppercase;

}

.service-model-top h2 span{
    font-family: var(--font-monExtra);
    color: rgba(209, 6, 6, 0.781);
}

.service-model-shapes{
    
    
}

.each-service-shapes{
    position: absolute;
    
    border-radius: 10rem;
    border: 1px solid rgba(219, 4, 4, 0.842);
}

.wave.service{
    position: absolute;
    
}


.wave.service:first-child{
    left: 20.5vw;
    top: 10vh;
}



.wave.service:nth-child(2){
    right: 20.5vw;
    bottom: 2vh;
  
}

.wave.service:nth-child(3){
    left: 20.5vw;
    bottom: 2vh;
 
}

.wave.service:nth-child(4){
    right: 20.5vw;
    bottom: 2vh;
   
}

.each-service-shapes img{
    width: 100%;
    border-radius: 10rem;
    object-fit: cover;
    height: 100%;
    transform: translate(2% , -2%);
}

.service-model-bottom{
    padding: 4vh 4.1vw;
    display: flex;
    height: 100%;
    width: 100%;
    gap: 3vw;
}



.service-packages h2{
 
    font-size: 4vh;
    color: rgba(224, 9, 9, 0.747);
}

.service-packages h3{
    font-size: 2vh;
}

.service-packages h4{
   text-decoration: underline;

}


	

/* -----------------------------------accordion gallery------------------------accordion gallery------------------------------------------------------------
  -------------------------------------------------------------------------------------------------------------------------------*/

        
        .containery {
            display: flex;
            justify-content: center;
            align-items: center;
        
            overflow: hidden;
        
        }
  
  .containery .cardy {
    flex: 1;
   
    transition: all 1s ease-in-out;
    height: 75vh;
    position: relative;
  }
  
  .containery .cardy .card__heady {
    color: black;
    background: rgba(255, 30, 173, 0.75);
    padding: 0.5em;
    transform: rotate(-90deg);
    transform-origin: 0% 0%;
    transition: all 0.5s ease-in-out;
    min-width: 100%;
    text-align: center;
    position: absolute;
    bottom: 0;
    left: 0;
    font-size: 2em;
    white-space: nowrap;
  }
  
  .containery .cardy:hover {
    flex-grow: 10;
  }
  
  .containery .cardy:hover img {
    filter: grayscale(0);
  }
  
  .containery .cardy:hover .card__heady {
    text-align: center;
    top: calc(100% - 2em);
    color: white;
    background: rgba(0, 0, 0, 0.5);
    font-size: 2em;
    transform: rotate(0deg) skew(-5deg);
  }
  
  .containery .cardy img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: all 1s ease-in-out;
    filter: grayscale(100%);
  }
  
  .containery .cardy:not(:nth-child(5)) {
    margin-right: 1em;
  }

  .containery .cardy .pricing-details {
    position: absolute;
    color: white;

    opacity: 0;
 
    z-index: 1; /* Ensure it's above the background */
    transition: opacity 0.5s ease; /* Add transition for smooth effect */
    display: flex;
  
   
    gap: 3vh;
    letter-spacing: 3px;
    flex-direction: column;
    text-align: center;
    background-color: hsla(0, 0%, 0%, 0.651);
  }
  .pricing-details::before,
.pricing-details::after {
  content: "";
  left: 10%;
  height: 1px;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.685);
  margin: 0px 0; /* Adjust the margin as needed */

}

  .containery .cardy:hover .pricing-details {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    left: 10%;
    top: 5%;
    
    z-index: 1; /* Ensure it's above the background */
    opacity: 1; /* Hide the content on hover */
  }
  
  .containery .cardy .pricing-details h4,
  .containery .cardy .pricing-details h3 {
    transition: color 0.5s ease; /* Add transition for smooth effect */
  }
  



